o;\n    font-size: 1.5em;\n    color: $light-color;\n    animation: deskripsi 3s ease;\n  }\n\n  p {\n    margin-top: 20px;\n    font-size: 1em;\n    color: $light-color;\n    animation: deskripsi 2s ease;\n  }\n}\n\n@keyframes garis {\n  0% {\n    width: 0;\n    border: 0 solid $third-color;\n    margin: 0 auto 0 auto;\n  }\n  100% {\n    width: 10%;\n    border: 1px solid $secondary-color;\n    margin: 0 auto 0 auto;\n  }\n}\n\n@keyframes deskripsi {\n  0% {\n    opacity: 0;\n  }\n  100% {\n    opacity: 1;\n  }\n}\n\n@keyframes nama {\n  from {\n    opacity: 0;\n    color: $light-color;\n  }\n  to {\n    opacity: 1;\n    color: $four-color;\n  }\n}\n","/* explore */\n@import \"root\";\nbody {\n  background-color: #10141b;\n}\n\nmain {\n  width: 100%;\n  margin: 0 auto;\n}\n\n.container {\n  margin: 2em 5%;\n  padding: 1em;\n  border-radius: 10px;\n  background-color: $primary-color;\n  box-shadow: 0px 0px 10px 0px $secondary-color;\n}\n\n.container-title h2 {\n  text-align: center;\n  margin-bottom: 1.6rem;\n  margin-top: 1rem;\n  color: $secondary-color;\n  font-family: $quicksand-font;\n}\n.error {\n  text-align: center;\n  color: $err;\n  font-weight: bold;\n  font-style: italic;\n}\n\n.cards {\n  display: grid;\n  gap: 20px;\n}\n\n.card {\n  background-color: $primary-color;\n  text-align: center;\n  border: 1px solid $secondary-color;\n  border-radius: 3px;\n  box-shadow: 0 0 2px $shadow-color-card;\n  position: relative;\n}\n\n.card .icon img {\n  width: 100%;\n  height: 200px;\n}\n\n.text {\n  padding: 15px;\n}\n\n.title {\n  font-weight: bold;\n  color: $secondary-color;\n}\n\n.title-city {\n  font-weight: 300;\n}\n\n.description {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-line-clamp: 2;\n  -webkit-box-orient: vertical;\n}\n\n.card .rate {\n  position: absolute;\n  padding: 10px;\n  display: flex;\n  justify-content: center;\n  color: $light-color;\n  background-color: $secondary-color;\n  border-bottom-right-radius: 10px;\n}\n\n.card .rate img {\n  width: 20px;\n}\n\n.card .rate p {\n  margin: 0 10px;\n  font-weight: bold;\n}\n.card .detail {\n  text-decoration: none;\n  padding: 10px;\n  justify-content: center;\n  color: $light-color;\n  background-color: $secondary-color;\n  cursor: pointer;\n  min-width: $min-size;\n  min-height: $min-size;\n}\n.contact {\n  width: 90%;\n  height: auto;\n  margin: 2em 5%;\n  display: flex;\n  flex-wrap: wrap;\n  padding: 10px;\n  border-radius: 10px;\n  background-color: $light-color;\n  box-shadow: 0px 0px 10px 0px $shadow-color;\n}\n\n.contact-map,\n.contact-form {\n  width: 100%;\n  height: auto;\n  flex: 100%;\n}\n\n.contact-map iframe {\n  width: 100%;\n  height: 100%;\n}\n\n.contact-form {\n  text-align: center;\n  padding: 30px;\n}\n\n.contact-form h2 {\n  margin-bottom: 10px;\n}\n\n.contact-form-text,\n.contact-form-textarea {\n  width: 100%;\n  height: 40px;\n  color: $dark-color;\n  border: 1px solid $border-input;\n  border-radius: 50px;\n  outline: none;\n  margin-bottom: 20px;\n  padding: 15px;\n  min-width: $min-size;\n  min-height: $min-size;\n}\n\n.contact-form-text::placeholder,\n.contact-form-textarea::placeholder {\n  color: $five-color;\n}\n\n.contact-form-textarea {\n  height: 130px;\n  border-radius: 10px;\n}\n\n.contact-form-btn {\n  width: 100%;\n  border: none;\n  border-radius: 50px;\n  background-color: $secondary-color;\n  color: $light-color;\n  text-transform: uppercase;\n  padding: 10px 0;\n  cursor: pointer;\n  font-size: 18px;\n  min-width: $min-size;\n  min-height: $min-size;\n}\n.contact-form-btn:hover {\n  background-color: $hover-btn-satu;\n}\n","@import \"root\";\n.wrapper {\n  position: fixed;\n  width: 100%;\n  z-index: 3;\n}\n\n.burger {\n  display: inline-block;\n  cursor: pointer;\n  z-index: 2;\n  outline: none;\n  border: none;\n  background-color: transparent;\n  min-width: $min-size;\n  min-height: $min-size;\n}\n.bar1,\n.bar2,\n.bar3 {\n  width: 44px;\n  height: 5px;\n  background-color: $bar-color;\n  margin: 6px 0;\n  transition: 0.4s;\n  border-radius: 5px;\n}\n\n.change .bar1 {\n  -webkit-transform: rotate(-45deg) translate(-9px, 6px);\n  transform: rotate(-45deg) translate(-9px, 6px);\n  background-color: $light-color;\n}\n\n.change .bar2 {\n  opacity: 0;\n  background-color: $light-color;\n}\n\n.change .bar3 {\n  -webkit-transform: rotate(45deg) translate(-8px, -8px);\n  transform: rotate(45deg) translate(-8px, -8px);\n  background-color: $light-color;\n}\n\n.container-flex {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\nnav {\n  position: sticky;\n  padding: 10px 20px;\n  box-shadow: 1px 2px 10px 2px rgba(0, 0, 0, 0.1);\n  background-color: $primary-color;\n\n  .brand a {\n    font-size: 1.4rem;\n    text-decoration: none;\n    font-weight: 750;\n    color: $secondary-color;\n    padding: 15px 20px; /* Menyesuaikan padding untuk memenuhi syarat touch target */\n    min-width: $min-size;\n    min-height: $min-size;\n  }\n}\n\n.bg-sidebar {\n  top: 0;\n  right: -100%;\n  bottom: 0;\n  width: 100%;\n  position: fixed;\n  background: rgba(0, 0, 0, 0.1);\n  transition: 0.3s;\n  visibility: $hidden-flow;\n\n  &.change {\n    right: 0;\n    visibility: visible;\n    display: block;\n  }\n}\n\nnav ul {\n  display: flex;\n  position: fixed;\n  background-color: $background-nav-responsive;\n  top: 0;\n  bottom: 0;\n  right: -300px;\n  width: 300px;\n  transition: 0.5s;\n  align-items: flex-start;\n  flex-direction: column;\n\n  &.change {\n    right: 0;\n    transition: 0.5s;\n  }\n\n  li {\n    list-style: none;\n    padding: 20px 15px;\n\n    &:hover::after {\n      content: \"\";\n      display: block;\n      width: 100%;\n      height: 3px;\n      transition: 0.3s;\n      background-color: $secondary-color;\n      transform: translateY(20px);\n    }\n\n    a {\n      color: #fff;\n      font-size: 1.2rem;\n      text-decoration: none;\n      padding: 15px 20px; /* Menyesuaikan padding untuk memenuhi syarat touch target */\n      min-width: $min-size;\n      min-height: $min-size;\n    }\n  }\n}\n","@import \"root\";\n@media screen and (min-width: 768px) {\n  .cards {\n    display: grid;\n    grid-template-columns: 1fr 1fr;\n  }\n  .fav-cards {\n    grid-template-columns: repeat(2, 1fr);\n  }\n  .contact-map,\n  .contact-form {\n    flex: 50%;\n  }\n}\n\n@media screen and (min-width: 960px) {\n  .bg-sidebar.change {\n    right: 0;\n    visibility: $hidden-flow;\n    display: none;\n  }\n\n  .burger {\n    display: none;\n  }\n\n  nav {\n    position: relative;\n  }\n\n  nav ul {\n    display: flex;\n    position: relative;\n    background-color: $reset;\n    top: $reset;\n    bottom: $reset;\n    right: $reset;\n    width: $reset;\n    flex-direction: row;\n  }\n\n  nav ul li {\n    padding: 10px 15px;\n    min-width: $min-size;\n  }\n\n  nav ul li:hover::after {\n    content: \"\";\n    display: block;\n    width: 100%;\n    transition: 0.3s;\n    background-color: $secondary-color;\n  }\n\n  nav ul li a {\n    color: $light-color;\n    font-size: 1.1rem;\n    min-height: $min-size;\n    min-width: $min-size;\n  }\n\n  nav ul li:last-child {\n    font-weight: 550;\n  }\n\n  .cards {\n    display: grid;\n    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));\n  }\n  .fav-cards {\n    grid-template-columns: repeat(3, 1fr);\n  }\n}\n","@import 'root';\n.detail {\n\tdisplay: grid;\n\tgrid-gap: 1.545em;\n\tfont-size: 1em;\n\tmargin-top: 1em;\n\th3 {\n\t\tfont-size: 1.5em;\n\t\tpadding: 1em 0 0 0;\n\t\tfont-weight: bold;\n\t\tposition: relative;\n\t\tcolor: $secondary-color;\n\t}\n\t.rating-detail {\n\t\tpadding: 0.3em 1em;\n\t\tmargin: 5px 0;\n\t\tdisplay: inline-block;\n\t\tcolor: $light-color;\n\t\tbackground-color: $secondary-color;\n\t\tmargin-right: 2px;\n\t\tborder: 1px solid transparent;\n\t\tborder-radius: 10px;\n\t}\n\t.categories {\n\t\tmargin-top: 20px;\n\t}\n\t.category {\n\t\tpadding: 0.3em 1em;\n\t\tcolor: $light-color;\n\t\tbackground-color: $secondary-color;\n\t\tmargin-right: 2px;\n\t\tborder: 1px solid transparent;\n\t\tborder-radius: 10px;\n\t\tdisplay: inline-block;\n\t\tmargin-top: 5px;\n\t}\n}\n.detail-img {\n  min-width: 95%;\n\twidth: 100%;\n\theight: 500px;\n\tobject-fit: fill;\n\tobject-position: center;\n\tborder-radius: 10px;\n\tborder: 1px solid $shadow-color;\n}\n.info {\n  min-width: 95%;\n\th3 {\n\t\tmargin-bottom: 10px;\n\t}\n\ti {\n\t\tmargin-bottom: 10px;\n\t\tdisplay: block;\n\t\tmargin-top: 5px;\n\t\tcolor: $light-color;\n\t}\n\tp {\n\t\tdisplay: block;\n\t\tmargin-top: 5px;\n\t\tcolor: $light-color;\n\t}\n}\n.garis-bawah {\n\t&::after {\n\t\tcontent: \"\";\n\t\tdisplay: block;\n\t\tposition: absolute;\n\t\tleft: 0;\n\t\tbottom: 0;\n\t\twidth: 7%;\n\t\theight: 2px;\n\t\tbackground-color: $dark-color;\n\t\tmargin: -2px auto;\n\t}\n}\n.detail-description {\n\ttext-align: justify;\n\tcolor: $grey;\n\tmargin-bottom: 0.5em;\n\tfont-style: italic;\n}\n.detail-menu {\n  min-width: 95%;\n\th4 {\n\t\tfont-weight: bold;\n\t\tfont-size: 1.37em;\n\t\tpadding: 1em;\n\t\tcolor: $light-color;\n\t}\n}\n.detail-food {\n\tdisplay: flex;\n\tflex-direction: column;\n\ttext-align: center;\n\talign-content: center;\n}\n.detail-drink {\n\tdisplay: flex;\n\tflex-direction: column;\n\ttext-align: center;\n\talign-content: center;\n}\n.cool-list {\n\tlist-style: none;\n\tpadding: 0;\n\tli {\n\t\tpadding: 15px 20px;\n\t\tmargin-bottom: 10px;\n\t\tbackground-color: $light-color-list;\n\t\tborder-radius: 5px;\n\t\tbox-shadow: 0px 2px 5px $shadow-color-dua;\n\t\ttransition: all 0.3s ease;\n\t\t&:hover {\n\t\t\ttransform: translateY(-3px);\n\t\t\tbox-shadow: 0px 4px 10px $shadow-color-tiga;\n\t\t}\n\t}\n}\n\n// tombol like\n.like {\n\tbackground-color: $secondary-color;\n\tcolor: $light-color;\n\tborder: 0;\n\tborder-radius: 50%;\n\twidth: 55px;\n\theight: 55px;\n\tcursor: pointer;\n\tdisplay: flex;\n\talign-items: center;\n\tjustify-content: center;\n\tposition: fixed;\n\tbottom: 16px;\n\tright: 16px;\n\tz-index: 10;\n\tfont-size: 16px;\n  \n\t&:hover {\n\t  background-color: $hover-btn-satu;\n\t}\n  }\n",".fav-cards {\n    display: grid;\n    gap: 20px;\n    grid-template-columns: repeat(auto-fit, minmax(auto, 1fr));\n  }\n  \n  .pesan-favorite {\n    text-align: center;\n    font-style: italic;\n  }\n  ","$dur: 1s;\n\n.wheel-and-hamster {\n  --dur: #{$dur};\n  position: relative;\n  width: 12em;\n  height: 12em;\n  font-size: 14px;\n  margin: 20px auto;\n\n  .wheel,\n  .hamster,\n  .hamster div,\n  .spoke {\n    position: absolute;\n  }\n\n  .wheel,\n  .spoke {\n    border-radius: 50%;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n  }\n\n  .wheel {\n    background: radial-gradient(100% 100% at center, hsla(0, 0%, 60%, 0) 47.8%, hsl(0, 0%, 60%) 48%);\n    z-index: 2;\n  }\n\n  .hamster {\n    animation: hamster $dur ease-in-out infinite;\n    top: 50%;\n    left: calc(50% - 3.5em);\n    width: 7em;\n    height: 3.75em;\n    transform: rotate(4deg) translate(-0.8em, 1.85em);\n    transform-origin: 50% 0;\n    z-index: 1;\n  }\n\n  .hamster__head {\n    animation: hamsterHead $dur ease-in-out infinite;\n    background: #00adb5;\n    border-radius: 70% 30% 0 100% / 40% 25% 25% 60%;\n    box-shadow: 0 -0.25em 0 hsl(40, 10%, 94%) inset, 0.75em -1.55em 0 hsl(30, 90%, 90%) inset;\n    top: 0;\n    left: -2em;\n    width: 2.75em;\n    height: 2.5em;\n    transform-origin: 100% 50%;\n  }\n\n  .hamster__ear {\n    animation: hamsterEar $dur ease-in-out infinite;\n    background: hsl(40, 10%, 94%);\n    border-radius: 50%;\n    box-shadow: -0.25em 0 #00adb5 inset;\n    top: -0.25em;\n    right: -0.25em;\n    width: 0.75em;\n    height: 0.75em;\n    transform-origin: 50% 75%;\n  }\n\n  .hamster__eye {\n    animation: hamsterEye $dur linear infinite;\n    background-color: hsl(0, 0%, 0%);\n    border-radius: 50%;\n    top: 0.375em;\n    left: 1.25em;\n    width: 0.5em;\n    height: 0.5em;\n  }\n\n  .hamster__nose {\n    background: hsl(0, 90%, 75%);\n    border-radius: 35% 65%